<template>
  <div class="search-box">
      <i class="icon-search"></i>
      <input type="box" :placeholder="placeholder" v-model="query"/>
      <i v-show="query" @click.native="clear" class="icon-dismiss">X</i>
      
  </div>
</template>
<script>
export default {
    props: {
        placeholder: {
            type: String,
            default: '搜索技能'
        }
    },
    data(){
        return {
            query:''
        }
    },
    methods: {
        clear(){
            this.query =''
        }
    },
    created() {
        this.$watch('query',(newQuery) => {
            this.$emit('query',newQuery)
        })
    }
  
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import "../common/stylus/variable"

  .search-box
    display: flex
    align-items: center
    box-sizing: border-box
    width: 100%
    padding: 0 6px
    height: 40px
    background: $color-highlight-background
    border-radius: 6px
    .icon-search
      font-size: 24px
      color: $color-background
    .box
      flex: 1
      margin: 0 5px
      line-height: 18px
      background: $color-highlight-background
      color: $color-text
      font-size: $font-size-medium
      &::placeholder
        color: $color-text-d
    .icon-dismiss
      font-size: 16px
      color: $color-background
</style>


